const ImgFreeDisp = props => {

    let content = (
        <div className={props.className}>
            <div className="w-full h-auto">
                <img src={props.src} alt={props.alt} />
                {(props.displayAlt)
                    ? (<p className="font-lato font-regular text-sm text-gray-500">
                        {props.alt}
                    </p>)
                    : null}
            </div>
        </div>
    )

    return content
}

export default ImgFreeDisp